/* sanitize.css v3.3.0 | CC0 1.0 Public Domain | github.com/10up/sanitize.css */

/*  Latest tested: Android 6, Chrome 48, Edge 13, Firefox 44, Internet Explorer 11, iOS 9, Opera 35, Safari 9, Windows Phone 8.1 */

/*
 * Normalization
 */

abbr[title] {
	text-decoration: underline; /*  Chrome 48+, Edge 12+, Internet Explorer 11-, Safari 9+ */
	text-decoration: underline dotted; /*  Firefox 40+ */
}

audio:not([controls]) {
	display: none; /*  Chrome 44-, iOS 8+, Safari 9+ */
}

b,
strong {
	font-weight: bolder; /*  Edge 12+, Safari 6.2+, and Chrome 18+ */
}

button {
	-webkit-appearance: button; /*  iOS 8+ */
	overflow: visible; /*  Internet Explorer 11- */
}

button,
input {
	&::-moz-focus-inner {
		border: 0; /*  Firefox 4+ */
		padding: 0; /*  Firefox 4+ */
	}

	&:-moz-focusring {
		outline: 1px dotted ButtonText; /*  Firefox 4+ */
	}
}

button,
select {
	text-transform: none; /*  Firefox 40+, Internet Explorer 11- */
}

details {
	display: block; /*  Edge 12+, Firefox 40+, Internet Explorer 11-, Windows Phone 8.1+ */
}

hr {
	overflow: visible; /*  Internet Explorer 11-, Edge 12+ */
}

html {
	-ms-overflow-style: -ms-autohiding-scrollbar; /*  Edge 12+, Internet Explorer 11- */
	overflow-y: scroll; /*  All browsers without overlaying scrollbars */
	-webkit-text-size-adjust: 100%; /*  iOS 8+, Windows Phone 8.1+ */
}

input {
	-webkit-border-radius: 0; /*  iOS 8+ */

	&[type='button'],
	&[type='reset'],
	&[type='submit'] {
		-webkit-appearance: button; /*  iOS 8+ */
	}

	&[type='number'] {
		width: auto; /*  Firefox 36+ */
	}

	&[type='search'] {
		-webkit-appearance: textfield; /*  Chrome 45+, Safari 9+ */

		&::-webkit-search-cancel-button,
		&::-webkit-search-decoration {
			-webkit-appearance: none; /*  Chrome 45+, Safari 9+ */
		}
	}
}

main {
	display: block; /*  Android 4.3-, Internet Explorer 11-, Windows Phone 8.1+ */
}

pre {
	overflow: auto; /*  Internet Explorer 11- */
}

progress {
	display: inline-block; /*  Internet Explorer 11-, Windows Phone 8.1+ */
}

summary {
	display: block; /*  Firefox 40+, Internet Explorer 11-, Windows Phone 8.1+ */
}

svg:not(:root) {
	overflow: hidden; /*  Internet Explorer 11- */
}

template {
	display: none; /*  Android 4.3-, Internet Explorer 11-, iOS 7-, Safari 7-, Windows Phone 8.1+ */
}

textarea {
	overflow: auto; /*  Edge 12+, Internet Explorer 11- */
}

[hidden] {
	display: none; /*  Internet Explorer 10- */
}

/*
 * Universal inheritance
 */

*,
::before,
::after {
	box-sizing: inherit;
}

* {
	font-size: inherit;
	line-height: inherit;
}

::before,
::after {
	text-decoration: inherit;
	vertical-align: inherit;
}

button,
input,
select,
textarea {
	font-family: inherit;
	font-style: inherit;
	font-weight: inherit;
}

/*
 * Opinionated defaults
 */

/*  specify the margin and padding of all elements */

* {
	margin: 0;
	padding: 0;
}

/*  specify the border style and width of all elements */

/*  ハイライトを消す */
/*  :not(button), */
*,
::before,
::after {
	border-style: solid;
	border-width: 0;
}

/*  remove the tapping delay from clickable elements */

a,
area,
button,
input,
label,
select,
textarea,
[tabindex] {
	touch-action: manipulation;
}

/*  specify the standard appearance of selects */

select {
	-moz-appearance: none; /*  Firefox 40+ */
	-webkit-appearance: none; /*  Chrome 45+ */

	&::-ms-expand {
		display: none; /*  Edge 12+, Internet Explorer 11- */
	}

	&::-ms-value {
		color: currentColor; /*  Edge 12+, Internet Explorer 11- */
	}
}

/*  use current current as the default fill of svg elements */

svg {
	fill: currentColor;
}

/*  specify the progress cursor of updating elements */

[aria-busy='true'] {
	cursor: progress;
}

/*  specify the pointer cursor of trigger elements */

[aria-controls] {
	cursor: pointer;
}

/*  specify the unstyled cursor of disabled, not-editable, or otherwise inoperable elements */

[aria-disabled] {
	cursor: default;
}

/*  specify the style of visually hidden yet accessible elements */

[hidden][aria-hidden='false'] {
	clip: rect(0 0 0 0);
	display: inherit;
	position: absolute;

	&:focus {
		clip: auto;
	}
}

/*
 * Configurable defaults
 */

/*  specify the core styles of all elements */

* {
	background-repeat: if(
		variable-exists(background-repeat),
		$background-repeat,
		no-repeat
	);
}

/*  specify the root styles of the document */

:root {
	background-color: if(
		variable-exists(root-background-color),
		$root-background-color,
		#fff
	);
	box-sizing: if(
		variable-exists(root-box-sizing),
		$root-box-sizing,
		border-box
	);
	color: if(variable-exists(root-color), $root-color, #000);
	cursor: if(variable-exists(root-cursor), $root-cursor, default);
	/*  font: #{if(variable-exists(root-font-size), $root-font-size, 100%)}/#{if(variable-exists(root-line-height), $root-line-height, 1.5)} if(variable-exists(root-font-family), $root-font-family, sans-serif); */
}

/*  specify the text decoration of anchors */

a {
	text-decoration: if(
		variable-exists(anchor-text-decoration),
		$anchor-text-decoration,
		none
	);
}

/*  specify the alignment of media elements */

audio,
canvas,
iframe,
img,
svg,
video {
	vertical-align: if(
		variable-exists(media-element-vertical-align),
		$media-element-vertical-align,
		middle
	);
}

/*  specify the background color of form elements */

/*  ハイライトを消す */
button,
input,
select,
textarea {
	background-color: if(
		variable-exists(form-element-background-color),
		$form-element-background-color,
		transparent
	);
	color: if(variable-exists(form-element-color), $form-element-color, inherit);
}

/*  specify the minimum height of form elements */

button,
[type='button'],
[type='date'],
[type='datetime'],
[type='datetime-local'],
[type='email'],
[type='month'],
[type='number'],
[type='password'],
[type='reset'],
[type='search'],
[type='submit'],
[type='tel'],
[type='text'],
[type='time'],
[type='url'],
[type='week'],
select,
textarea {
	min-height: if(
		variable-exists(form-element-min-height),
		$form-element-min-height,
		1.5em
	);
}

/*  specify the font family of code elements */

code,
kbd,
pre,
samp {
	font-family: if(
			variable-exists(monospace-font-family),
			$monospace-font-family,
			monospace
		),
		monospace;
}

/*  specify the list style of nav lists */

nav ol,
nav ul {
	list-style: if(variable-exists(nav-list-style), $nav-list-style, none);
}

/*  specify the font size of small elements */

small {
	font-size: if(variable-exists(small-font-size), $small-font-size, 75%);
}

/*  specify the border styling of tables */

table {
	border-collapse: if(
		variable-exists(table-border-collapse),
		$table-border-collapse,
		collapse
	);
	border-spacing: if(
		variable-exists(table-border-spacing),
		$table-border-spacing,
		0
	);
}

/*  specify the resizability of textareas */

textarea {
	resize: if(variable-exists(textarea-resize), $textarea-resize, vertical);
}

